<template>
  <v-chart
    ref="echartsRef"
    class="relation-graph"
    :option="chartOption"
    :autoresize="true"
  ></v-chart>
</template>

<script setup>
import resources from '@/assets/resources'
import { ref, defineProps, toRefs, watch, onMounted } from 'vue'
import VChart from 'vue-echarts'
import { use } from 'echarts/core'
import { GraphChart } from 'echarts/charts'
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent
} from 'echarts/components'
import { CanvasRenderer } from 'echarts/renderers'
import * as echarts from 'echarts'

// ECharts
use([GraphChart, TitleComponent, TooltipComponent, LegendComponent, CanvasRenderer])

// props
const props = defineProps({
  selectedCVData: {
    type: Object,
    required: true
  }
})

const { selectedCVData } = toRefs(props)

// ECharts
const echartsRef = ref(null)

// 
const nodes = ref([])
const links = ref([])
const centerNode = ref('')

// watchselectedCVData
watch(selectedCVData, (newValue) => {
  // 
  centerNode.value = newValue.CVName.toString()
  // 
  nodes.value = [...newValue.KG.nodes]
  links.value = [...newValue.KG.links]
  // 
  updateChartOption()
}, { deep: true })

// 
function updateChartOption () {
  if (echartsRef.value) {
    // nodes.value.slice()
    echartsRef.value.setOption({
      title: {
        // text: ''
      },
      legend: {
        top: '90%', // 
        left: 'center',
        icon: 'circle', // 
        data: [centerNode.value, '', '', '', '', '', '']
      },
      textStyle: {
        fontFamily: 'opposans' // 
      },
      tooltip: {
        formatter: function (x) {
          return x.data.description // 
        }
      },
      series: [{
        type: 'graph',
        layout: 'force',
        force: {
          repulsion: 300, // 
          gravity: 0.1,
          edgeLength: [10, 20]
        },
        animation: false,
        label: {
          show: true,
          position: 'right'
        },
        edgeLabel: {
          fontSize: 8,
          normal: {
            show: true,
            formatter: (params) => params.data.score
          }
        },
        draggable: true,
        roam: true,
        scaleLimit: {
          min: 0.1,
          max: 2
        },
        data: nodes.value.slice(), // 
        links: links.value.slice(), // 
        categories: [
          { name: centerNode.value, itemStyle: { color: getRadialGradient('#a9adf9', resources.themeColor), borderWidth: 1, borderColor: resources.themeColor } },
          { name: '', itemStyle: { color: getLinearGradient('#ee6666', '#fbd8c3') } },
          { name: '', itemStyle: { color: getLinearGradient('#fec27d', '#f8d6ce') } },
          { name: '', itemStyle: { color: getLinearGradient('#92abf0', '#b1e9f7') } },
          { name: '', itemStyle: { color: getLinearGradient('#64b8b8', '#cdf0b0') } },
          { name: '', itemStyle: { color: getLinearGradient('#7fc984', '#f6fbbb') } },
          { name: '', itemStyle: { color: getLinearGradient('#92a2e9', '#c1e1f1') } },
          { name: '', itemStyle: { color: getLinearGradient('#9adffa', '#aab6e1') } }
        ]
      }]
    })
  }
}
// 
function getLinearGradient (color1, color2) {
  return new echarts.graphic.LinearGradient(0, 0, 1, 0, [
    { offset: 0, color: color1 },
    { offset: 1, color: color2 }
  ])
}
// 
function getRadialGradient (color1, color2) {
  return new echarts.graphic.RadialGradient(0.5, 0.5, 1.0, [
    {
      offset: 0,
      color: color1
    },
    {
      offset: 1,
      color: color2
    }
  ])
}
// watch
onMounted(() => {
  // 
  centerNode.value = selectedCVData.value.CVName.toString()
  nodes.value = selectedCVData.value.KG.nodes
  links.value = selectedCVData.value.KG.links
  updateChartOption() // 

  // 
  if (echartsRef.value) {
    echartsRef.value.resize()
  }
})
</script>

<style scoped>
.relation-graph {
  height: 30rem;
  width: 100%;
}
</style>
