<template>
  <teleport to="body" >
    <div v-if="showModal" class="overlay"></div>
    <div v-if="showModal" class="login-card" >
      <div class="tabs" >
        <button
        :class="{ active: userType === '1' }"
        @click="setUserType('1')"
        >
          
        </button>
        <button
        :class="{ active: userType === '2' }"
        @click="setUserType('2')"
        >
          
        </button>
      </div>

      <div class="login-form">
        <input type="text" placeholder="" v-model="username" style="margin-top: 0.5rem;"/>
        <div class="input-group">
          <input type="password" placeholder="" v-model="password" style="margin-top: 0.5rem;width: 12rem"/>
          <button @click="sendCode" style="background:var(--themeColor01);color:var(--themeColor);margin-top: 0.5rem;margin-left: 0.6rem;width: 9rem;height: 100%">{{ sendingButtonText }}</button>
        </div>
        <button @click="login" style="margin-top: 1rem;"> / </button>
      </div>
    </div>
  </teleport>
</template>

<script setup>
import { ref, defineEmits, computed } from 'vue'
import router from '@/router'
import { fakeUserList } from '@/components/Tools/js/fakeData'

const userType = ref('1') // 
const username = ref('')
const password = ref('')

const setUserType = (type) => {
  userType.value = type
}

//  Vue  ref 
const isLoggedIn = ref(false) // 
const showModal = ref(true) // 

const emit = defineEmits(['login-success'])

const login = () => {
  if (userType.value === '1') {
    // 
    console.log('', username.value, password.value)

    // TODO: 
    // setTimeout
    setTimeout(() => {
      // 
      isLoggedIn.value = true
      // 
      showModal.value = false
      // 
      // userInfo.value = response.data.user;

      console.log('')
      emit('login-success', fakeUserList[0])
    }, 500)
  }
  if (userType.value === '2') {
    // 
    console.log('', username.value, password.value)
    // TODO: 
    // setTimeout
    setTimeout(() => {
      // 
      isLoggedIn.value = true
      // 
      showModal.value = false
      // 
      // userInfo.value = response.data.user;
      console.log('')
      emit('login-success', fakeUserList[0])
    }, 500)
    // TODO
    router.push('/mainpage2')
  }
}
// 
const isSendingCode = ref(false) // 
const countDown = ref(0) // 

// 
const sendingButtonText = computed(() => {
  return countDown.value > 0 ? `(${countDown.value}s)` : ''
})

const sendCode = () => {
  if (isSendingCode.value || countDown.value > 0) return // 

  // TODO: API
  console.log('', username.value)

  // 
  isSendingCode.value = true
  countDown.value = 60 // 60
  const intervalId = setInterval(() => {
    countDown.value--
    if (countDown.value <= 0) {
      clearInterval(intervalId) // 
      isSendingCode.value = false // 
    }
  }, 1000)
}
</script>

<style scoped>
.login-card, :deep(.login-card) {
  font-family: 'opposans', sans-serif !important;
}
.input-group{
  display: flex;
  flex-direction: row;
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(1px);
  z-index: 999; /*  */
}
.login-card {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  border-radius: 1rem;
  box-shadow: 0 5px 15px 0 rgba(176,191,231,.4);
  padding: 30px;
  width: 400px;
  height: 330px;
  z-index: 1000;
  margin: 0;
}

.login-card .tabs {
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
}

.login-card .tabs button {
  background: none;
  border: none;
  padding: 10px;
  cursor: pointer;
  transition: color 0.3s;
}

.login-card .tabs button.active {
  color: var(--themeColor);
}

.login-card .tabs button:focus {
  outline: none;
}

.login-card .login-form {
  display: flex;
  flex-direction: column;
}

.login-card .login-form input {
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid var(--themeColor02);
  border-radius: 0.5rem;
}

.login-card .login-form input:focus {
  outline: none;
  border-color: var(--themeColor);
}

.login-card .login-form button {
  padding: 10px;
  border: none;
  border-radius: 0.5rem;
  background-color: var(--themeColor);
  color: #fff;
  cursor: pointer;
  transition: background-color 0.3s;
}

.login-card .login-form button:hover {
  background-color: var(--themeColor075);
}

.login-card .login-form button:focus {
  outline: none;
}

</style>
