<template>
  <div class="navbar" :class="{ 'sticky': isSticky }">
    <div class="logo">
<!--      <img :src="resources.logo" alt="Logo" />-->
      <img :src="resources.logo" alt="Logo" />
    </div>
    <div class="links">
      <router-link class="nav-link" :to="{ name: 'mainpage' }" active-class="active"></router-link>
      <router-link class="nav-link" :to="{ name: 'cv' }" active-class="active"></router-link>
    </div>
    <a-button v-if="!isUserLoggedIn" @click="showLoginModal" class="btn" :style="{ borderColor: resources.themeColor, color: resources.themeColor }"> / </a-button>
    <div v-else class="user-avatar" @click="gotoUserPage" @mouseenter="toggleDropdown(true)" @mouseleave="toggleDropdown(false)">
      <img :src="userAvatar" alt="User Avatar" />
      <div v-if="showDropdown" class="dropdown">
        <div @click="logout"></div>
      </div>
    </div>
    <loginCard v-if="showModal" @close="showModal = false" @login-success="handleLoginSuccess"></loginCard>
  </div>
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount, onBeforeMount } from 'vue'
import resources from '@/assets/resources'
import router from '@/router'
import LoginCard from '@/components/Login/LoginCard.vue'

const isUserLoggedIn = ref(false) // 
const userAvatar = ref('https://www.gravatar.com/avatar/?d=mp') //  URL
const showModal = ref(false) // 
const isSticky = ref(false) // 

// 
const showLoginModal = () => {
  showModal.value = true
}

// 
const handleLoginSuccess = (userData) => {
  showModal.value = false
  isUserLoggedIn.value = true
  userAvatar.value = userData.avatar //  userData  URL
  // localStorage
  localStorage.setItem('isUserLoggedIn', 'true')
  localStorage.setItem('userAvatar', userData.avatar)
  localStorage.setItem('userInfo', JSON.stringify(userData))
}

// 
const handleScroll = () => {
  isSticky.value = window.pageYOffset > 0
}

// 
onBeforeMount(() => {
  isUserLoggedIn.value = localStorage.getItem('isUserLoggedIn') === 'true'
  userAvatar.value = localStorage.getItem('userAvatar') || resources.avatar
})

// 
onMounted(() => {
  window.addEventListener('scroll', handleScroll)
})

// 
onBeforeUnmount(() => {
  window.removeEventListener('scroll', handleScroll)
})

// 
const gotoUserPage = () => {
  router.push('/user')
}
// 
const showDropdown = ref(false)

const toggleDropdown = (show) => {
  showDropdown.value = show
}

const closeDropdown = (event) => {
  if (!event.target.closest('.user-avatar')) {
    showDropdown.value = false
  }
}

onMounted(() => {
  window.addEventListener('scroll', handleScroll)
  document.addEventListener('click', closeDropdown)
})

onBeforeUnmount(() => {
  window.removeEventListener('scroll', handleScroll)
  document.removeEventListener('click', closeDropdown)
})
const logout = () => {
  isUserLoggedIn.value = false
  userAvatar.value = 'https://www.gravatar.com/avatar/?d=mp'
  localStorage.removeItem('isUserLoggedIn')
  localStorage.removeItem('userAvatar')
  router.push('/')
}
</script>

<style scoped>
.user-avatar img {
  /*  */
  max-height: 2rem; /*  */
  max-width: 2rem; /*  */
  border-radius: 50%; /*  */
  object-fit: cover; /*  */
  margin-right: 10.3rem;
}
.user-avatar {
  position: relative;
  cursor: pointer;
}
.user-avatar .dropdown {
  position: absolute;
  top: 100%;
  right: 8rem;
  background-color: white;
  border-radius: 4px;
  box-shadow: 0 5px 15px 0 rgba(176,191,231,.4);
  width: 4rem;
  height: 2rem;
  z-index: 1001;
  display: flex;
  justify-content: center;
  align-items: center;
  color:var(--greyFontColor)
}

.user-avatar:hover .dropdown {
}
/*  */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  height: 2.75rem;
  background-color: rgba(255, 255, 255, 0.4);
  background-size:cover;
  backdrop-filter: blur(10px);
  z-index: 999;
  transition: background-color 0.3s;
  box-shadow: 0 5px 10px 0 rgba(176,191,231,.5);
  padding: 0 16px;
}
.logo img {
  height: 42px;
  margin-right: 1rem; /*  */
}
.links {
  display: flex;
  flex-grow: 1;
  justify-content: center;
}
.links a {
  margin: 0 0.75rem;
  color: var(--blackFontColor);
  font-size: 16px;
  text-decoration: none;
}

/* / */
@media (max-width: 768px) {
  .logo img {
    height: 36px; /*  Logo */
  }
  .links {
    display: none; /*  */
  }
  .navbar.sticky {
    padding: 0 8px; /*  */
  }
}

/* / */
@media (min-width: 768px) and (max-width: 1024px) {
  .links {
    display: flex; /*  */
    margin-right: auto; /*  */
  }
}

/* / */
@media (min-width: 1024px) {
  .logo img {
    margin-left: 2rem; /*  */
  }
  .links a {
    margin: 0 1.5rem; /*  */
  }
  .btn {
    margin-right: 6rem; /* / */
  }
}

/*  */
.btn {
  margin-left: auto; /*  */
  border-color: #4852e6 !important;
  color: var(--themeColor) !important;
  background: rgba(0, 0, 0, 0);
}

.btn:hover {
  border-color: var(--themeColor) !important;
  color: var(--themeColor) !important;
  background-color: var(--themeColor02) !important;
}
.links a.active {
  color: var(--themeColor); /*  */
}
.links a:hover {
  color: var(--themeColor); /*  */
}
/*  */
.active {
  color: var(--themeColor);
}

/*  */
.nav-link {
  margin: 0 0.75rem;
  color: var(--blackFontColor);
  font-size: 16px;
  text-decoration: none;
}

.nav-link:hover {
  color: var(--themeColor);
}
</style>
