    value: '',
    label: ''
  },
  {
    value: '',
    label: ''
  },
  {
    value: '',
    label: ''
  }
])
const handleChangeEducation = value => {
  // console.log(`selected ${value}`)
}
const emit = defineEmits(['update:education'])
